html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button, input[type=submit] {
  outline: 0;
}

.block {
  display: block;
}

.i-block {
  display: inline-block;
}

.flex {
  display: flex;
}

.i-flex {
  display: inline-flex;
}

.flex.fdc, .i-flex.fdc {
  flex-direction: column;
}
.flex.aic, .i-flex.aic {
  align-items: center;
}
.flex.aife, .i-flex.aife {
  align-items: flex-end;
}
.flex.jcc, .i-flex.jcc {
  justify-content: center;
}
.flex.jcfe, .i-flex.jcfe {
  justify-content: flex-end;
}
.flex.jcsb, .i-flex.jcsb {
  justify-content: space-between;
}
.flex.aifs, .i-flex.aifs {
  align-items: flex-start;
}
.flex.ais, .i-flex.ais {
  align-items: stretch;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.abs {
  position: absolute;
}

.rel {
  position: relative;
}

.fix {
  position: fixed;
}

.m-center {
  margin: 0 auto;
}

.w100 {
  width: 100%;
}

.pointer-events-none {
  pointer-events: none;
}

.bg-icon {
  display: inline-block;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
}

.caps {
  text-transform: uppercase;
}

body {
  font-family: "Open Sans", sans-serif;
}

.container {
  max-width: 1110px;
  width: 100%;
  margin: 0 auto;
}

.title {
  font-family: "Oswald", sans-serif;
}

.arrows {
  border: 2px solid #005BC1;
  position: relative;
}
.arrows::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 2px;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  background-color: #005BC1;
  z-index: 2;
}
.arrows .arrow {
  cursor: pointer;
  background-image: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.5 20L5.3881 10.5L11.5 1' stroke='%23005BC1' stroke-width='2'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: 50%;
  width: 55px;
  height: 55px;
  transition: 0.2s ease;
}
.arrows .arrow.next-arrow {
  transform: rotate(180deg);
}
.arrows .arrow:hover {
  background-image: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.5 20L5.3881 10.5L11.5 1' stroke='%23fff' stroke-width='2'/%3E%3C/svg%3E%0A");
  background-color: #005BC1;
}

.section {
  margin-top: 150px;
}

.section-title {
  display: flex;
  align-items: center;
  font-size: 30px;
  line-height: 44px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #343434;
}
.section-title::before {
  content: "";
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/title-lines.svg");
  margin-right: 40px;
  width: 100px;
  height: 13px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background: rgba(0, 91, 193, 0.6);
  z-index: 9;
  transition: background 0.3s ease;
}
.header.full-bg {
  background: #005bc1;
}
.header .container {
  height: 100%;
}
.header .nav {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .nav .logo {
  background-image: url("../img/logo.svg");
  width: 140px;
  height: 58px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: opacity 0.2s ease;
}
.header .nav .logo:hover {
  opacity: 0.8;
}
.header .nav .links .item {
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #fff;
  transition: opacity 0.2s ease;
}
.header .nav .links .item:not(:last-child) {
  margin-right: 30px;
}
.header .nav .links .item:hover {
  opacity: 0.8;
}

.welcome {
  padding-top: 80px;
  background-image: url("../img/top-bg.jpg");
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
}
.welcome .welcome-content {
  padding-top: 200px;
  padding-bottom: 200px;
  padding-left: 190px;
  position: relative;
}
.welcome .welcome-content::before {
  content: "";
  background-color: rgba(0, 91, 193, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 444px;
  height: 100%;
  z-index: 0;
}
.welcome .welcome-content .title {
  position: relative;
  z-index: 1;
  font-size: 48px;
  line-height: 71px;
  letter-spacing: 0.01em;
  color: #FFFFFF;
  margin-bottom: 40px;
}
.welcome .welcome-content .description {
  position: relative;
  z-index: 1;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  margin-bottom: 40px;
}
.welcome .welcome-content .button {
  position: relative;
  z-index: 1;
  text-decoration: none;
  border: 2px solid #FFFFFF;
  padding: 15px 40px;
  display: inline-block;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #FFFFFF;
  text-transform: uppercase;
  transition: 0.2s ease;
}
.welcome .welcome-content .button:hover {
  background-color: #fff;
  color: #005BC1;
}

.section-works {
  /* the slides */
  /* the parent */
}
.section-works .title-line {
  margin-bottom: 65px;
}
.section-works .slick-slide {
  margin: 0 15px;
}
.section-works .slick-list {
  margin: 0 -15px;
}
.section-works .slider .slide .img {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 255px;
  padding-bottom: 35px;
  display: flex;
  justify-content: flex-end;
  padding-right: 35px;
}
.section-works .slider .slide .img .info {
  background-color: #fff;
  padding: 20px 25px;
  max-width: 250px;
  color: #343434;
}
.section-works .slider .slide .img .info .name {
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  margin-bottom: 12px;
}
.section-works .slider .slide .img .info .description {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
}

.section-catalog > .title {
  margin-bottom: 70px;
}
.section-catalog .tabs-titles {
  font-size: 24px;
  line-height: 36px;
  text-transform: uppercase;
  margin-bottom: 55px;
}
.section-catalog .tabs-titles .item {
  transition: 0.2s ease-in-out;
  cursor: pointer;
  user-select: none;
}
.section-catalog .tabs-titles .item:not(:last-child) {
  margin-right: 50px;
}
.section-catalog .tabs-titles .item.active {
  color: #005BC1;
}
.section-catalog .tabs-titles .liner {
  background-color: #005BC1;
  height: 2px;
  bottom: -3px;
}
.section-catalog .tabs-contents .item {
  overflow: hidden;
}
.section-catalog .tabs-contents .item .content-items .content-item {
  max-width: calc((100% - 60px) / 3);
  width: 100%;
}
.section-catalog .tabs-contents .item .content-items .content-item .img {
  border: 1px solid #005BC1;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: contain;
  padding-top: 86%;
  width: 100%;
  max-width: 100%;
  position: relative;
}
.section-catalog .tabs-contents .item .content-items .content-item .img .name {
  position: absolute;
  bottom: 15px;
  right: 0;
  background-color: #005BC1;
  color: #fff;
  text-transform: uppercase;
  left: 85px;
  font-family: "Oswald";
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  padding: 15px 21px;
  min-height: 86px;
  display: flex;
  align-items: center;
}

.section-rent .title {
  margin-bottom: 40px;
}
.section-rent .rent-layout .text {
  max-width: 350px;
  font-weight: 300;
  font-size: 18px;
  line-height: 25px;
  color: #343434;
}
.section-rent .rent-layout .slider {
  max-width: calc(100% - 445px);
  padding: 30px;
  border: 3px solid #005BC1;
}
.section-rent .rent-layout .slider .name-line {
  margin-bottom: 30px;
}
.section-rent .rent-layout .slider .name-line .name {
  font-family: "Oswald";
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #343434;
  margin-right: 70px;
}
.section-rent .rent-layout .slider .slider-items .item .slide-content .img {
  width: 255px;
  height: 250px;
  background-position: 50%;
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 30px;
}
.section-rent .rent-layout .slider .slider-items .item .slide-content .info .info-title {
  margin-bottom: 30px;
  font-family: "Oswald";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #343434;
}
.section-rent .rent-layout .slider .slider-items .item .slide-content .info .info-features .item {
  display: flex;
  align-items: center;
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: #343434;
  max-width: 281px;
}
.section-rent .rent-layout .slider .slider-items .item .slide-content .info .info-features .item:not(:last-child) {
  margin-bottom: 19px;
}
.section-rent .rent-layout .slider .slider-items .item .slide-content .info .info-features .item::before {
  content: "";
  display: inline-block;
  width: 4px;
  min-width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: #343434;
  margin-right: 16px;
}

.section-news {
  padding-bottom: 120px;
}
.section-news .title-line {
  margin-bottom: 70px;
}
.section-news .title-line .view-more {
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 15px 34px;
  display: inline-block;
  background-color: #005bc1;
  transition: 0.2s ease;
  cursor: pointer;
  border: 1px solid #005BC1;
}
.section-news .title-line .view-more:hover {
  background-color: #fff;
  color: #005BC1;
}
.section-news .news-items {
  display: grid;
  grid-template-columns: 49% repeat(2, 1fr);
  gap: 60px;
}
.section-news .news-items .item {
  width: 100%;
}
.section-news .news-items .item-thick {
  display: flex;
  align-items: flex-start;
}
.section-news .news-items .item-thick .img {
  width: 255px;
  height: auto;
}
.section-news .news-items .item-thick .text {
  margin-left: 30px;
}
.section-news .news-items .item-thick .text .name-date {
  margin-bottom: 42px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.section-news .news-items .item-thick .text .name-date .date {
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #979797;
}
.section-news .news-items .item-thick .read-full {
  font-size: 14px;
  line-height: 19px;
  text-decoration-line: underline;
  color: #343434;
}
.section-news .news-items .item-thick .read-full:hover {
  text-decoration: none;
}
.section-news .news-items .item-thin .name {
  margin-bottom: 16px;
  min-height: 54px;
  max-height: 54px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.section-news .news-items .item-thin .short {
  max-height: 38px;
  min-height: 38px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.section-news .news-items .item-thin .img-date {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.section-news .news-items .item-thin .img-date .img {
  max-width: 128px;
  width: 100%;
  height: auto;
}
.section-news .news-items .item-thin .img-date .date {
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: #979797;
}
.section-news .news-items .item .name {
  font-family: "Oswald";
  font-size: 18px;
  line-height: 27px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #343434;
}
.section-news .news-items .item .short {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: #343434;
  margin-bottom: 20px;
}

.footer {
