  max-width: 551px;
}
.footer .footer-nav .item {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  text-decoration: none;
  margin-bottom: 10px;
  align-self: flex-start;
  max-width: 190px;
}
.footer .footer-nav .item:hover {
  text-decoration: underline;
}
.footer .footer-bottom .copy {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: #fff;
}
.footer .footer-bottom .by {
  font-weight: 300;
  font-size: 14px;
  line-height: 19px;
  color: #fff;
}
.footer .footer-bottom .by a {
  color: #fff;
  text-decoration: none;
}
.footer .footer-bottom .by a:hover {
  text-decoration: underline;
}